@import url(https://fonts.googleapis.com/css?family=Quicksand);html, body {
  margin:0;
  height:100%;
  font-family:"Quicksand";
}

.app {
  background:#4DC5F7;
  height:100vh;
  width:100vw;
  display:flex;
  flex-direction:column;
  align-items:center;
  justify-content:center;
}

.app > * {
  flex-shrink:0;
}

.it {
  --margin:15px;

  margin-top:var(--margin);
  margin-bottom:var(--margin);
  border-radius:3px;
  background:white;
  box-shadow:0px 0px 2.5px 0px rgba(0, 0, 0, .2);
  width:630px;
}

.it.empty {
  box-shadow:inherit;
  background:transparent;
}
.display {
    height:70px;
    position:relative;
    background:transparent;
    overflow:hidden;
    box-shadow:inherit;
    background:white;
    display:flex;
    align-items:center;
    justify-content:space-evenly;
}

.display > span {
    width:100px;
    text-align:center;
}

.display-restart {
    height:30px;
    width:85px;
    cursor:pointer;
    background:black;
    border-radius:3px;
    background:transparent;
    border:1px solid rgba(0, 0, 0, .1);
    transition:all .25s, border .15s;
}

.display-restart:focus {
    border:1px solid rgba(0, 0, 255, .1);
}

.display-restart:active {
    transform:scale(.95);
    border:1px solid rgba(0, 255, 0, .1);
}
.board {
    height:580px;
    width:600px;
    display:grid;
    grid-template-columns:repeat(4, 1fr);
    grid-gap:20px;
    position:relative;
    align-items:center;
}

.board-end {
    position:absolute;
    top:50%;
    transform:translateY(-50%);
    height:45px;
    font-size:1.5em;
    color:white;
    font-family:"Quicksand";
    color:white;
    width:100%;
    text-align:center;
}
.display-card {
    height:130px;
    width:135px;
    position:relative;
    box-shadow:inherit;
    perspective:800px;
    opacity:1;
    transition:opacity .4s .75s;
}

.display-card.hidden {
    opacity:0;
}

.display-card-side {
    position:absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
    background:white;
    border-radius:3px;
    backface-visibility:hidden;
    transition:.4s;
}

.display-card-front {
    cursor:pointer;
}

.display-card-back {
    transform:rotateY(180deg);
    display:flex;
    align-items:center;
    justify-content:center;
}

.display-card.rotated > .display-card-front {
    transform:rotateY(180deg);
}

.display-card.rotated > .display-card-back {
    transform:rotateY(360deg);
}

.display-card-back-icon {
    width:50%;
    object-fit:cover;
    object-position:center;
}
